.table {
  max-height: 60vh;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
  background-color: #ffffff;
  background-color: var(--bracket-seed);
  overflow-y: scroll;
  overflow-x: hidden;
  padding-bottom: 48px;
  /* remember youve added padding here! */
}

.table::-webkit-scrollbar {
  width: 12px; /* width of the entire scrollbar */
}
.table::-webkit-scrollbar-track {
  background: #d8d8d8; /* color of the tracking area */
  background-clip: content-box;
}
.table::-webkit-scrollbar-thumb {
  background-color: #4a4a4a;
  background-color: var(--item-text); /* color of the scroll thumb */
  border-radius: 20px; /* roundness of the scroll thumb */
  border: 3px solid #d8d8d8; /* creates padding around scroll thumb */
  height: 20px;
}

.table-arrow-1,
.table-arrow-2,
.table-arrow-3,
.table-arrow-4,
.table-arrow-5,
.table-arrow-6 {
  padding-right: 8px;
  cursor: pointer;
}
