/* new manager profile */

.new-manager-profile #contact > .box {
  display: flex;
  flex-direction: column;
}
.new-manager-profile #contact > .box > div {
  border-radius: 3px;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
  background-color: var(--bracket-seed);
  display: flex;
  flex-direction: row;
  margin: 4px 0px;
}

#manager_email::placeholder {
  /* padding-left: 110px; */
  text-align: right;
}

#manager_email::-webkit-input-placeholder {
  text-align: right;
}

@media (min-width: 768px) {
  #manager_email::-webkit-input-placeholder {
    padding-left: 110px;
  }
}

.new-manager-profile #contact > .box > div > img {
  margin: 10px 0px 8px 8px;
}

.new-manager-profile #contact > .box > div > .first-div {
  margin: 5px 0px 8px 14px;

  height: 24px;
  font-family: FuturaMedium;
  font-size: 12px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 24px;
  letter-spacing: normal;
  color: #4a4a4a;
  display: flex;
  align-content: center;
}

.new-manager-profile #contact > .box > div > .last-div {
  margin: 5px 0px 8px 14px;

  height: 24px;
  font-family: Futura;
  font-size: 12px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 24px;
  letter-spacing: normal;
  text-align: end;
  color: #4a4a4a;
  /* width:200px; */
}

