.red_highlight {
  border-radius: 3px;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
  /* background-color: var(--bracket-seed); */
  display: flex;
  flex-direction: row;
  margin: 4px 0px;

  border-left: 4px solid #ff2072;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
}

.manager_profile_image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: #d8d8d8;
  overflow: visible;
  display: inline-block;
  margin-left: 0px;
  position: relative;
  object-fit: cover;
}

.manager_profile_image_default {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: #d8d8d8;
  overflow: visible;
  display: inline-block;
  margin-left: 0px;
  position: relative;
  padding: 10px;
  object-fit: contain;
}

.inactive_dot {
  width: 25px !important;
  height: 25px;
  border: solid 4px #f9fafc;
  background-color: #9b9b9b;
  border-radius: 50%;
  display: inline-block;
  position: absolute;

  /* left: 60px;
  top: 145px; */
  left: 60px;
  top: 159px;
}

.inactive_text {
  color: #747474;
  margin-left: 350px;
  display: flex;
  justify-content: flex-end;
  /* margin-bottom: 20px; */
}

.profile_successfully_deleted {
  width: 398px;
  height: 230px;
  display: flex;
  justify-content: center;
  align-items: center;
}

