.dropdown_animation {
  animation: growOut 300ms ease-in-out alternate both;
  transform-origin: top right;
  /* animation: shrinkIn 200ms ease-in-out alternate both; */
}

@keyframes growOut {
  0% {
    transform: scale(0);
  }
  80% {
    transform: scale(1);
  }
  100% {
    transform: scale(1);
  }
}

