  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: var(--item-text);
  margin-top: 64px;
  margin-left: 83px;
  margin-bottom: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Subheading {
  width: 398px;
  height: 38px;
  font-family: Futura;
  font-size: 10px;
