.FormInput {
  width: 460px;
  height: 32px;
  border-radius: 3px;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
  background-color: var(--bracket-seed);
  outline: none;
  border: none;
  margin: 39px 0px 0px 52px;
}

.input-modal-style {
  padding-left: 5px;
  font-family: Futura;
  font-size: 16px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #747474;
}

.input-modal-style::placeholder {
  padding-left: 5px;
  font-family: Futura;
  font-size: 16px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 16px;
  letter-spacing: normal;
  color: #747474;
}

::placeholder {
  width: 188px;
  height: 16px;
  font-family: Futura;
  font-size: 12px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: var(--item-text);
  padding: 0%;
}

.PSubmitButton {
  width: 112px;
  height: 24px;
  margin: 18px 8px 14px 16px;
  padding: 6px 27px 5px;
  opacity: 1;
  border-radius: 15px;
  border: solid 1px var(--ffd-420);
  background-color: var(--ffd-420);
  outline: none;

  margin-left: 436px;
  margin-top: 52px;
  font-family: 'Futura';
  src: local('Futura'),
    url(/src/assets/fonts/Futura/futura\ medium\ bt.ttf) format('truetype');
  font-size: 10px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: var(--item-text);

  outline: 0px;
}

.PS {
  position: relative;
