/* hamburger */
#score-hamburger {
  cursor: context-menu;
  text-decoration: none;
  position: relative;
  display: inline-block;
  flex-direction: row;
  justify-content: flex-end;
  outline: 0px;
  border: 0px;
}
#score-hamburger > div > img {
  cursor: pointer;
}

#score-hamburger > div > span {
  display: inline-block;
  background-color: #ffffff;
  /* visibility: hidden; */
  position: absolute;
  top: 19px;
  right: -5px;
  min-width: 142px;
  width: max-content;
  padding: 9px 0;
  border-radius: 12px;
  box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.2);
  overflow: hidden;
  z-index: 10;
}

/* hamburger tooltip */
#score-hamburger > div > div {
  display: inline-block;
  position: relative;
  visibility: hidden;
  background-color: #ffd420;
  width: 94px;
  height: 24px;
  padding: 6px 4.6px 5px 5px;
  border-radius: 3px;
  font-family: Futura;
  font-size: 10px;
  font-weight: 500;
  bottom: 100%;
  right: -42%;
  color: #4a4a4a;
  top: -45px;
}

#score-hamburger > div > div::after {
  content: '';
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #ffd420 transparent transparent transparent;
}

#score-hamburger:hover > div > div {
  visibility: visible;
}

/* #score-hamburger:focus > div > span

{
  visibility: visible;
} */

#score-hamburger > div > span > ul {
  margin: 0px;
  list-style-type: none;
  padding: 0px;
}

#score-hamburger > div > span > ul > li {
  height: 30px;
  text-align: right;
  padding-right: 10px;
  line-height: 30px;
  color: #4a4a4a;

