.wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.profile {
  max-width: 180px;
  /* max-width: 120px; */
  max-height: 28px;
  font-family: Futura;
  font-size: 16px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 16px;
  letter-spacing: normal;
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  border-bottom: 2px solid #9b9b9b;
  color: #9b9b9b;
}

.history {
  max-width: 180px;
  /* max-width: 120px; */
  max-height: 28px;
  font-family: Futura;
  font-size: 16px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 16px;
  letter-spacing: normal;
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  border-bottom: 2px solid #9b9b9b;
  color: #9b9b9b;
}

.profile:hover,
.history:hover {
  color: #9b9b9b;
}

#active-profile {
  min-height: 100vh;
}

@media (max-width: 767px) {
  .new-event-profile .wrapper {
    width: 300px;
  }
  .profile > p,
  .history > p {
    width: 120px;
    height: 15px;
  }
  #active-profile {
    width: 300px;
  }
}

@media (min-width: 768px) {
  .new-event-profile .wrapper {
    width: 460px;
  }
  .profile > p,
  .history > p {
    width: 120px;
    height: 15px;
  }
  #active-profile {
    width: 460px;
  }
}

@media (min-width: 992px) {
  .profile > p,
  .history > p {
    width: 180px;
    height: 28px;
  }
}

/* active tab */
/* #active-profile {
  padding-bottom: 258px;
} */
#active-profile > #score-hamburger {
  cursor: context-menu;
  text-decoration: none;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-top: 58px;
}
#active-profile > #score-hamburger > div > img {
  cursor: pointer;
}

#active-profile > #score-hamburger > div > span {
  display: inline-block;
  background-color: #ffffff;
  visibility: hidden;
  position: absolute;
  top: 19px;
  right: 0px;
  width: 142px;
  padding: 9px 0;
  border-radius: 12px;
  box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.2);
  overflow: hidden;
  z-index: 10;
}

