#active-profile > #score-hamburger:focus > div > span {
  visibility: visible;
}

#active-profile > #score-hamburger > div > span > ul {
  margin: 0px;
  list-style-type: none;
  padding: 0px;
}

#active-profile > #score-hamburger > div > span > ul:focus {
  visibility: hidden;
}

#active-profile > #score-hamburger > div > span > ul > li {
  height: 30px;
  text-align: right;
  padding-right: 10px;
  line-height: 30px;
  color: #4a4a4a;

  font-family: Futura;
  font-size: 12px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;

  font-family: FuturaMedium;
}

#active-profile > #score-hamburger > div > span > ul > li:hover {
  background-color: #ffd420;
  font-weight: bold;
  cursor: pointer;
}

