/* history */

/* #active-history {
  height: 900px;
} */

#active-history > .container > #history-search-bar {
  overflow: hidden;
  margin: 40px 0px 16px 0px;
  width: 324px;
  height: 32px;
  padding: 0 130px 0 16px;
  border-radius: 20px;
  border: solid 1px #d8d8d8;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
}

#active-history > .container > #history-search-bar > input {
  width: 152px;
  height: 32px;
  margin: 0 0 0 8px;
  opacity: 0.5;
  font-family: Futura;
  font-size: 12px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 32px;
  letter-spacing: normal;
  color: #4a4a4a;
  border: 0px;
  outline: 0px;
}

#active-history > .container > #history-data {
  /* width: 1120px; */
  /* height: 273px; */
  /* margin: 16px 0px 0px 80px; */
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
  background-color: #ffffff;
  /* overflow-y: scroll; */
}

#active-history > .container > #history-data > table {
  display: block;
  border-collapse: collapse;
  text-align: center;
}

#active-history > .container > #history-data > table th {
  width: 186px;
  height: 31px;
  font-family: Futura;
  font-size: 10px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #9b9b9b;
  border-bottom: 1px solid #d8d8d8;
}

#active-history > .container > #history-data > table td {
  width: 186px;
  height: 31px;
  font-family: Futura;
  font-size: 12px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4a4a4a;
  padding: 14px 0px 14px 0px;
}

#active-history > .container > #history-data > table img {
  width: 24px;
  height: 24px;
  margin-left: 40px;
}

#active-history > .container > #history-data > table span {
  line-height: 31px;
}

#active-history > .container > #history-data > table > thead > tr > th > span {
  cursor: pointer;
}

#active-history
  > .container
  > #history-data
  > table
  > thead
  > tr
  > th
  > span
  > img {
  width: 8px;
  height: 8px;
  margin-left: 0px;
  margin-right: 5px;
  display: inline-block;
  position: relative;
  transition-property: transform;
  transition-duration: 300ms;
}

