/* :root{
    --bracket-seed:#ffffff;
    --item-text:#4a4a4a;
    --colors-dark-grey:#9b9b9b;
    --eeeeee:#eeeeee;
    --line:#333333;
}

@font-face{
    font-family: 'Futura';
    src: local('Futura'), url(/src/assets/fonts/Futura/futura\ medium\ bt.ttf) format('truetype');
}

*,
*::before,
*::after {
  box-sizing: border-box;
    outline: 1px solid red;
}

.DashBoard{
    width: 1120px;

    position: absolute;
    display: inline-block;
    align-content: center;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: #f9fafc;
    margin: 76px 80px 38px 80px;
}

.Quick-Start {
    display: flex;
    align-items: center;

    width: 118px;
    height: 36px;
    font-family: Futura;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: var(--item-text);
}

.IconFlex{
    display: flex;
    flex-direction: row;
}

.NewManager, .NewPlayer, .NewTournament{
    display: flex;
    flex-direction: column;
    position: relative;
}

.NewManager{
    margin-left: 71px;
}

.NewPlayer{
    margin-left: 240px;
}

.NewTournament{
    margin-left: 240px;
}

.Square1, .Square2, .Square3{
    width: 166px;
    height: 166px;
    opacity: 0;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
    background-color: var(--bracket-seed);
}


.Icon-Orange-Manager, .Icon-Orange-Player, .Icon-Orange-teams{
    position: absolute;
    top:19px;
