    letter-spacing: normal;
    color: var(--item-text);
    margin-bottom: 40px;
    
    display: flex;
    align-items: center;
}

.hr1{
    content: "";
    width: 953px;
    height: 1px;
    background-color: var(--line);
    margin-top: 58px;
    margin-left: 17px;
}

.hr2{
    content: "";
    width: 953px;
    height: 1px;
    background-color: var(--line);
    margin-top: 62px;
    margin-left: 16px;
}

.UpcomingEvents{
    width: 128px;
    height: 36px;
    font-family: Futura;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: var(--item-text);
    margin-top:35px;
    margin-bottom: 40px;
    display: flex;
    align-items: center;
}

.EventCard{
    width: 472px;
    height: 172px;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
    background-color: var(--bracket-seed);
}

.LeftCard{
    margin-left: 72px;
}

.RightCard{
    margin-left: 34px;
}

.CardVerticalFlex, .EventInfo{
    display: flex;
    flex-direction: column;
}

.CardInfo, .OEFlex, .UEFlex, .EventDetails, .CardButtonFlex{
