/* tanmay css*/

.dashboard-template .img-quick-start {
  height: 128px;
}

.dashboard-template .upper-box {
  color: #4a4a4a;
}

.dashboard-template .upper-box:focus {
  background-color: #ffffff;
  border-left: 4px solid #ffd420;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
}

.dashboard-template .upper-box:hover {
  background-color: #ffffff;
  border-left: 4px solid #ffd420;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
}

.dashboard-template .rectangle {
  width: 472px;
  padding: 16px 0 0;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
  background-color: var(--bracket-seed);
}

.dashboard-template .rectangle:focus {
  border-left: 4px solid #ffd420;
}

.dashboard-template .rectangle:hover {
  border-left: 4px solid #ffd420;
}

.dashboard-template .rectangle-heading {
  height: 32px;
  margin: 18px 0px 8px 0px;
  font-family: Futura;
  font-size: 14px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4a4a4a;
  border-bottom: 1px solid #979797;
  display: flex;
  align-items: center;
}

.dashboard-template .rectangle-content {
  width: 106px;
  height: 20px;
  font-family: Futura;
  font-size: 11px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4a4a4a;
}
.dashboard-template .rectangle-content {
  width: 145px;
  height: 20px;
  font-family: Futura;
  font-size: 11px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4a4a4a;
}

.span-1 {
  width: 106px;
  height: 20px;
  font-family: FuturaMedium;
  font-size: 11px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4a4a4a;
}

.span-2 {
  width: 145px;
  height: 20px;
  font-family: FuturaMedium;
  font-size: 11px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4a4a4a;
}

.dashboard-template .rectangle-footer {
  padding: 10px 0px;
  margin-top: 35px;
  font-family: Futura;
  font-size: 11px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
